import isEqual from 'fast-deep-equal/react';

export const filterBySubject = (data) => {
  const uniqueItems = [];

  data.forEach((day) => {
    day.data.forEach((item) => {
      const filteredItem = {
        CARRERA: item.CARRERA,
        ASIGNATURA: item.ASIGNATURA,
        SECCION: item["SECCIN"],
        TIPO_DE_CLASE: item["TIPO DE CLASE"],
      };

      const allFieldsAreEmpty = Object.values(filteredItem).every((field) => field === undefined || (typeof field === 'string' && field.trim().length === 0));

      if (!allFieldsAreEmpty) {
        const isDuplicate = uniqueItems.some((element) => isEqual(element, filteredItem));
        if (!isDuplicate) {
          uniqueItems.push(filteredItem);
        }
      }
    });
  });

  return uniqueItems;
};

export class classGroupDay {
  constructor(dataSheetDay = []) {
    this.data = dataSheetDay.data;
    this.id = dataSheetDay.id
  }

  getDayTitle() {
    return this.id;
  }

  getMainInformation() {
    return this.data.map(item =>
      `[${item.CARRERA}/${item.SEMESTRE}/Sec: ${item.SECCIN}/Tipo: ${item["TIPO DE CLASE"]}] ${item.ASIGNATURA}`);
  }

  getAllClassData() {
    const transformData = this.data.map(item => {
      const objectData = Object.assign({"Clase":`[${item.CARRERA}/${item.SEMESTRE}/Sec: ${item.SECCIN}/Tipo: ${item["TIPO DE CLASE"]}] ${item.ASIGNATURA}`}
      , item)
      return objectData
    })
    return transformData
  }
}
